<template>
	<div id="deliveryer-getcash">
		<public-header title="提现申请"></public-header>
		<div class="content">
			<div class="wrap-search wrap-search-input">
				<div class="tab-group flex-lr border-1px-b">
					<div class="tab-item" :class="{active: !filter.items.status}" @click="onToggleStatus(0)">全部</div>
					<div class="tab-item" :class="{active: filter.items.status == 2}" @click="onToggleStatus(2)">申请中</div>
					<div class="tab-item" :class="{active: filter.items.status == 1}" @click="onToggleStatus(1)">提现成功</div>
					<div class="tab-item" :class="{active: filter.items.status == 3}" @click="onToggleStatus(3)">撤销</div>
				</div>
				<van-search placeholder="请输入配送员姓名" v-model="filter.items.keyword" />
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
			<van-list
				v-model="records.loading"
				:finished="records.finished"
				@load="onLoad"
				:offset="100"
				:immediate-check="false"
				v-if="!records.empty"
				class="getcash-list"
			>
				<div class="getcash-item margin-10-b" v-for="(item, index) in records.data">
					<van-row>
						<van-col span="5">
							<div class="getcash-img">
								<img class="img-100" :src="item.avatar" alt="">
							</div>
						</van-col>
						<van-col span="19">
							<div class="getcash-name flex-lr">
								<div class="c-default">{{item.title}}</div>
								<div class="itag itag-primary font-14" v-if="item.status == 1">提现成功</div>
								<div class="itag itag-danger font-14" v-else-if="item.status == 2">申请中</div>
								<div class="itag itag-disabled font-14" v-else-if="item.status == 3">已撤销</div>
							</div>
							<div class="getcash-info flex">
								<div class="c-default font-14">提现金额</div>
								<div class="c-danger font-14">￥{{item.get_fee}}</div>
							</div>
							<div class="getcash-info flex">
								<div class="c-default font-14">手续费</div>
								<div class="c-danger font-14">￥{{item.take_fee}}</div>
							</div>
							<div class="getcash-info flex">
								<div class="c-default font-14">实际到账</div>
								<div class="c-danger font-14">￥{{item.final_fee}}</div>
							</div>
							<div class="getcash-info flex">
								<div class="c-default font-14">微信昵称</div>
								<div class="c-gray font-14">{{item.nickname}}</div>
							</div>
							<div class="getcash-info flex">
								<div class="c-default font-14">申请时间</div>
								<div class="c-gray font-14">{{item.addtime_cn}}</div>
							</div>
							<div class="getcash-info flex">
								<div class="c-default font-14">订单号</div>
								<div class="c-gray font-14">{{item.trade_no}}</div>
							</div>
							<div class="getcash-info flex" v-if="item.status == 1">
								<div class="c-default font-14">处理完成时间</div>
								<div class="c-gray font-14">{{item.endtime_cn}}</div>
							</div>
						</van-col>
					</van-row>
					<div class="border-1px-t btn-group" v-if="item.status == 2">
						<div class="btn-item bg-default" @click="onToogleRemarkPopup(item.id, index)">撤销</div>
						<div class="btn-item bg-default" @click="onGetcashUpdate(item.id, 'status', index, 1)">设为已处理</div>
						<div class="btn-item bg-primary" @click="onGetcashUpdate(item.id, 'transfers', index)">微信打款</div>
					</div>
				</div>
				<div class="loaded" v-if="records.finished">
					<div class="loaded-tips">没有更多了</div>
				</div>
			</van-list>
			<div class="no-data" v-else>
				<img src="static/img/order_no_con.png" alt="">
				<p>没有符合条件的数据!</p>
			</div>
			</van-pull-refresh>
		</div>
		<!--回复弹出层-->
		<van-popup v-model="remarkPopup" position="bottom">
			<div class="popup-title">
				<span class="c-gray" @click="onToogleRemarkPopup">取消</span>
				<span>撤销备注</span>
				<span class="c-primary" @click="onConfirmRemark">确定</span>
			</div>
			<div class="popup-content">
				<van-field
					v-model="remark"
					type="textarea"
					placeholder="输入撤销提现备注"
					rows="8"
				/>
			</div>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				showPreLoading:true,
				isRefresh: false,
				filter: {
					items: {
						status: 0
					}
				},
				confirms: {
					'cancel': '确定撤销该提现申请吗？',
					'status': '确定将该提现申请设为已处理吗？',
					'transfers': '确定微信打款吗？'
				},
				remarkPopup: false,
				remark: '',
				cancelId: 0,
				cancelIndex: -1,
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'plateform/deliveryer/getcash/list'
				 });
			},

			onPullDownRefresh() {
				this.onLoad(true);
			},

			onToggleStatus: function(value) {
				value = parseInt(value);
				if(this.filter.items.status == value) {
					return;
				}
				this.filter.items.status = value;
			},

			onToogleRemarkPopup(id, index) {
				this.remarkPopup = !this.remarkPopup;
				if(id > 0 && index > -1) {
					this.cancelId = id;
					this.cancelIndex = index;
				}
			},

			onConfirmRemark() {
				var that = this;
				if(!that.remark) {
					this.util.$toast('备注不能为空');
					return;
				}
				if(that.cancelId > 0 && that.cancelIndex > -1) {
					that.onGetcashUpdate(that.cancelId, 'cancel', that.cancelIndex);
				}
			},

			onGetcashUpdate(id, type, index, status) {
				var that = this;
				var tip = that.confirms[type];
				if(type == 'cancel' && !that.remark) {
					this.$toast('请输入备注');
				}
				var params = {
					url: 'plateform/deliveryer/getcash/status?id=' + id + '&type=' + type + '&status=' + status + '&remark=' + this.remark,
					confirm: tip,
					success: function(message) {
						that.util.$toast(message);
						if(that.filter.items.status == 0) {
							var changeStatus = 1;
							if(type == 'cancel') {
								changeStatus = 3;
							}
							that.records.data[index].status = changeStatus;
						} else {
							that.records.data.splice(index, 1);
						}
						that.records = Object.assign({}, that.records);
						that.remark = '';
						that.remarkPopup = false;
					}
				};
				that.util.jspost(params);
			}
		},

		mounted(){
			this.onLoad();
		},

		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			},
		}
	}
</script>

<style>
	#deliveryer-getcash .van-search{
		padding: 10px;
	}
	#deliveryer-getcash .content .getcash-item{
		background-color: #fff;
	}
	#deliveryer-getcash .content .getcash-item .getcash-img{
		width: 55px;
		height: 55px;
		padding-top: 10px;
		padding-left: 10px;
	}
	#deliveryer-getcash .content .getcash-item .getcash-img img{
		border-radius: 100%;
	}
	#deliveryer-getcash .content .getcash-item .getcash-name{
		padding-top: 10px;
		padding-right: 10px;
		height: 55px;
	}
	#deliveryer-getcash .content .getcash-item .getcash-info{
		padding-bottom: 8px;
	}
	#deliveryer-getcash .content .getcash-item .getcash-info:last-child{
		padding-bottom: 20px;
	}
	#deliveryer-getcash .content .getcash-item .getcash-info div:first-child{
		width: 40%;
	}
	#deliveryer-getcash .content .getcash-item .btn-group{
		display: flex;
		flex-wrap: nowrap;
		justify-content: flex-end;
		align-items: center;
		width: 100%;
		padding: 10px;
	}
	#deliveryer-getcash .content .getcash-item .btn-group .btn-item{
		min-width: 60px;
		margin-left: 10px;
		text-align: center;
		padding: 10px 8px;
		border: 1px solid #e5e5e5;
		border-radius: 3px;
		font-size: 14px;
	}
	#deliveryer-getcash .content .getcash-item .btn-group .bg-default{
		border: 1px solid #ccc;
	}
</style>


